import React, { useEffect, useMemo, useState } from 'react'
import classNames from 'classnames'
import { Menu } from 'antd'
import { MenuInfo } from 'rc-menu/lib/interface'
import { MenuItemType } from 'antd/lib/menu/hooks/useItems'
import { LeftOutlined, RightOutlined } from '@ant-design/icons'

import { doraemonClassName } from '../../util/internal'

import { LayoutMenuConfig, LayoutMenuItem } from './type'
import './index.scss'

export interface LayoutProps extends React.ComponentProps<any> {
  /**
   * @default 100%
   */
  height?: number | string
  header?: React.ReactNode
  footer?: React.ReactNode
  brand?: React.ReactNode
  /**
   * @description :root {
   *   --#{$prefix}-layout-dark-background-color: #181c27;
   *   --#{$prefix}-layout-dark-color: #d0d0d0;
   *   --#{$prefix}-layout-dark-border-color: rgb(152 188 252 / .06);
   *   --#{$prefix}-layout-dark-header-background-color: #1f3144;
   * }
   */
  dark?: boolean
  onClick?: (keys: string[]) => void
  collapsed?: boolean
  /**
   *  
   */
  defaultOpenKeys?: string[]
  selectedKeys?: string[]
  menu: LayoutMenuConfig
  menuClassName?: string
  menuStyle?: React.CSSProperties
}

const Layout = ({
  children,
  className,
  height = '100%',
  style,
  defaultOpenKeys: menuDefaultOpenKeys,
  selectedKeys,
  collapsed: menuCollapsed = false,
  onClick: menuOnClick,
  menu = [],
  brand,
  header,
  footer,
  dark = false,
  menuClassName,
  menuStyle,
  ...rest
}: LayoutProps) => {
  const [collapsed, setCollapsed] = useState<boolean>(menuCollapsed)
  useEffect(() => {
    setCollapsed(menuCollapsed)
  }, [menuCollapsed])

  const defaultOpenKeys = useMemo(() => {
    const openKeys: string[] = []
    if (!menuDefaultOpenKeys) {
      const getDefaultOpenKeys = (items: LayoutMenuItem[]): string[] => {
        if (!items || items.length === 0) return []
        const keys: string[] = []
        items.forEach((item) => {
          if (item.children && item.children.length > 0) {
            keys.push(item.key)
            getDefaultOpenKeys(item.children)
          }
        })
        return keys
      }
      openKeys.push(...getDefaultOpenKeys(menu))
    } else {
      openKeys.push(...menuDefaultOpenKeys)
    }
    return openKeys
  }, [])

  const handleMenuClick = ({ keyPath }: MenuInfo) => {
    menuOnClick && menuOnClick(keyPath)
  }

  const menuItems = useMemo(() => {
    const generateItems = (items: LayoutMenuItem[]): MenuItemType[] | null => {
      if (!items || items.length === 0) return null
      return (
        items?.map((item) => ({
          icon: item.icon,
          key: item.key,
          label: item.label,
          disabled: item.disabled,
          children: generateItems(item.children || []),
        })) || []
      )
    }
    return generateItems(menu)
  }, [menu])

  return (
    <div
      style={{ height: height, ...style }}
      className={classNames(
        doraemonClassName('layout'),
        {
          [doraemonClassName('layout-dark')]: dark,
        },
        className,
      )}
      {...rest}>
      {
        <div
          className={classNames(doraemonClassName('layout-header-wrapper'), {
            [doraemonClassName('layout-header-wrapper-dark')]: dark,
          })}>
          {brand && <div className={doraemonClassName('layout-brand')}>{brand}</div>}
          {header && <div className={doraemonClassName('layout-header')}>{header}</div>}
        </div>
      }
      <div className={doraemonClassName('layout-main')}>
        <div
          className={classNames(doraemonClassName('layout-menu-wrapper'), {
            [doraemonClassName('layout-menu-collapsed')]: collapsed,
            [doraemonClassName('layout-menu-wrapper-dark')]: dark,
          })}>
          <Menu
            style={{ flex: 1, ...menuStyle }}
            theme={dark ? 'dark' : 'light'}
            className={classNames(doraemonClassName('layout-menu'), menuClassName)}
            onClick={handleMenuClick}
            selectedKeys={selectedKeys}
            defaultOpenKeys={defaultOpenKeys}
            inlineCollapsed={collapsed}
            items={menuItems || []}
            mode={'inline'}
          />
          <div
            className={classNames(doraemonClassName('layout-collapsed-trigger'), {
              [doraemonClassName('layout-collapsed-trigger-dark')]: dark,
            })}
            onClick={() => setCollapsed(!collapsed)}>
            {collapsed ? <RightOutlined /> : <LeftOutlined />}
          </div>
        </div>
        <div
          className={classNames(doraemonClassName('layout-content-wrapper'), {
            [doraemonClassName('layout-content-wrapper-dark')]: dark,
          })}>
          <div className={doraemonClassName('layout-content')}>{children}</div>
          <div className={doraemonClassName('layout-footer')}>{footer}</div>
        </div>
      </div>
    </div>
  )
}

export default Layout
